.hownd-faq {
  position: relative;
  margin: 40px auto;
  max-width: 740px;
}
.hownd-faq__item-header {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
  padding: 15px 0 11px;
  font-family: "Block Berthold Regular";
  font-size: 15px;
  color: #1c2b42;
  text-transform: uppercase;
  cursor: pointer;
}
.hownd-faq__item-header .icon {
  color: #1c2b42;
  transition: 0.2s ease-in-out;
}
.hownd-faq__item-header .icon svg {
  width: 16px;
}
.hownd-faq__item-header[aria-expanded=true] .icon {
  transform: rotate(45deg);
}
.hownd-faq__item-header[aria-expanded=true]:after {
  opacity: 1;
}
.hownd-faq__item-header:after {
  content: "";
  position: absolute;
  top: 0;
  left: -18px;
  z-index: -1;
  width: calc(100% + 36px);
  height: 100%;
  background-color: #f8f8f8;
  opacity: 0;
  transition: 0.2s ease-in-out;
}
.hownd-faq__item-header:hover:after {
  opacity: 1;
}
.hownd-faq__item-content {
  padding-top: 20px;
  padding-bottom: 10px;
  font-size: 16px;
}
.hownd-faq__item-content a {
  color: #f7be24;
}
.hownd-faq__group-title {
  text-transform: uppercase;
}
.hownd-faq__group + .hownd-faq__group {
  margin-top: 45px;
}
.hownd-faq .btn-primary {
  font-size: 14px;
}
.hownd-faq__search {
  position: relative;
  margin-bottom: 50px;
}
.hownd-faq__search-input {
  display: block;
  padding: 12px 30px 12px 12px;
  width: 100%;
  font-family: "Block Berthold Regular";
  font-size: 15px;
  color: #395263;
  text-transform: uppercase;
  box-shadow: none;
  outline: 0;
  background-color: #f8f8f8;
  border: 0;
}
.hownd-faq__search-input::-webkit-input-placeholder {
  color: #395263;
}
.hownd-faq__search-input:-moz-placeholder {
  color: #395263;
}
.hownd-faq__search-input::-moz-placeholder {
  color: #395263;
}
.hownd-faq__search-input:-ms-input-placeholder {
  color: #395263;
}
.hownd-faq__search-icon {
  position: absolute;
  top: 50%;
  right: 10px;
  line-height: 0;
  transform: translateY(-50%);
}
.hownd-faq__sidebar {
  position: absolute;
  margin-left: -200px;
  width: 180px;
  text-transform: uppercase;
}
.hownd-faq__sidebar-title {
  margin-bottom: 40px;
}
.hownd-faq__sidebar li {
  padding-top: 10px;
  padding-bottom: 10px;
}
.hownd-faq__sidebar a {
  font-family: "Block Berthold Regular";
  font-size: 15px;
  color: #395263;
  text-decoration: none;
}

.autocomplete-suggestions {
  padding-top: 20px;
  padding-left: 10px;
  overflow-y: auto;
  font-family: "Block Berthold Regular";
  font-size: 15px;
  color: #395263;
  text-transform: uppercase;
  background-color: #fff;
  border: 1px solid #f8f8f8;
}
.autocomplete-suggestions .autocomplete-suggestion {
  padding-bottom: 10px;
  cursor: pointer;
}